  display: none;
}

.filter__form__custom-select {
  position: relative;
  display: inline-block;
}

.filter__form__custom-select-trigger {
  position: relative;
  display: block;
  min-width: 190px;
  padding-left: 12px;
  padding-right: 30px;
  padding-top: 4px;
  padding-bottom: 4px;
  font-weight: 600;
  font-size: 15px;
  line-height: 30px;
  color: #4b4747;
  background: #fff;
  cursor: pointer;
  border: 1px solid #e7a45e;
}

.filter__form__custom-select-trigger:after {
  position: absolute;
  display: block;
  content: "";
  width: 13px;
  height: 13px;
  top: 50%;
  right: 12px;
  margin-top: -3px;
  border-bottom: 2px solid #e7a45e;
  border-right: 2px solid #e7a45e;
  border-radius: 2px;
  transform: rotate(45deg) translateY(-50%);
  transition: all 0.4s ease-in-out;
  transform-origin: 50% 0;
}

