  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%);
}

.filter__form__custom-options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  min-width: 100%;
  margin-top: -1px;
  border: 1px solid #e7a45e;
  box-sizing: border-box;
  box-shadow: 0 2px 1px rgba(0, 0, 0, 0.07);
  background: #fff;
  transition: all 0.4s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px);
}

.filter__form__custom-select.opened .filter__form__custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(0);
}

