  position: relative;
  display: block;
  padding-left: 12px;
  font-weight: 600;
  font-size: 15px;
  line-height: 30px;
  color: #4b4747;
  line-height: 47px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

